$Theme: #2f2f2f;

.app {
    display: flex;
    justify-content: center;
    background-color: $Theme;

    .container {
        width: 100%;
        margin: auto;
        min-height: 20vh;

        // Login & Register
        .link {
            text-decoration: none;
            color: inherit;
        }

        .Login-box {
            font-family: Verdana;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            max-width: 700px;
            min-width: 650px;
            max-height: 70vh;
            min-height: 65vh;
            text-align: center;
            background-color: #101010;
            border-radius: 2vw;
            box-shadow: 2px 5px 10px black;
            opacity: 0.9;
            img {
                width: 200px;
            }
        }

        .auth {
            background-image: url('https://wallpapercat.com/w/full/4/6/1/1165730-3840x2160-desktop-4k-landscape-background.jpg');
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            min-height: 100vh;
            color: white;
            font-size: 1.2em;
        }

        h1 {
            opacity: 1;
        }

        label {
            font-size: 1.1em;
            
        }

        input {
            width: 100%;
            margin: 20px 0px;
            padding: 10px 0px;
            border: none;
            border-radius: 2vw;
        }

        

        button {
           
            // display: flex;
            // align-items: center;
            // justify-content: center;
            font-size: 1.5em;
            font-weight: 700;
            padding: 15px;
            color: white;
            transition: 0.5s;
            width: 80%;
            background-color: green;
            border: none;
            border-radius: 2vw;
            width: 400px;

            &:hover {
                scale: 110%;
                cursor: pointer;
            }
        }

        p {
            font-size: 12px;
            color: red;
            text-align: center;
        }

        span {
            font-size: 0.8em;
            text-align: center;
        }
    }

    // Navbar
    .navbar {
        .container {
            padding: 10px 0px;
            display: flex;
            width: 100%;
            align-items: center;
            flex-direction: row;
            justify-content: space-around;
            background-color: #101010;
            color: white;

            .logo {
                img {
                    width: 120px;
                    padding: 20px;
                    margin-right: 100px;
                    background-color: white;
                    border-radius: 2vw;
                }
            }

            .links {
                display: flex;
                align-items: center;
                gap: 30px;

                h6 {
                    font-size: 16px;
                    font-weight: 300;
                }

                span {
                    cursor: pointer;
                }
            }

            span {
                background-color: red;
                width: 250px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-basis: 150px;
                float: right;
                font-size: 1.7em;
                border-radius: 2vw;
                display: flex;
                justify-content: center;
                align-items: center;
                font-weight: 400;
                color: white;
                transition: 0.5s ease;

                &:hover {
                    transform: scale(120%);
                    font-weight: 3em;
                    cursor: pointer;
                }
            }

            .write {
                background-color: red;
                border-radius: 2vw;
                display: flex;
                justify-content: center;
                align-items: center;
                font-weight: 300;
                color: white;
                transition: 0.5s ease;

                &:hover {
                    transform: scale(120%);
                    font-weight: 3em;
                    cursor: pointer;
                }
            }
        }
        
    }

    // Footer
    .footer {
        color: white;
        display: flex;
        justify-content: center;
        width: 100%;
        align-items: center;
        background-color: #101010;
        padding: 70px 0;

        .row {
            display: flex;
            flex-wrap: wrap;
        }

        ul {
            list-style: none;
        }

        .footer-col {
            width: 25%;
            padding: 0px 15px;
        }

        .footer-col h4 {
            position: relative;
            font-size: 18px;
            color: white;
            text-transform: capitalize;
            margin-bottom: 35px;
            font-weight: 500;
        }

        .footer-col h4::before {
            content: '';
            position: absolute;
            left: 0;
            bottom: -10px;
            background-color: red;
            height: 2px;
            box-sizing: border-box;
            width: 125px;
        }

        .footer-col ul li:not(:last-child){
            margin-bottom: 10px;
        }
        .footer-col ul li a {
            font-size: 16px;
            text-transform: capitalize;
            color: rgb(163, 162, 162);
            font-weight: 400;
            text-decoration: none;
            display: block;
            transition: all 0.3 ease;
        }

        .footer-col ul li a:hover {
            color: white;
            padding-left: 8px;
        }

        .footer-col .social-links a {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            height: 20px;
            background-color: #101010 ;
            margin: 0 10px 10px 0;
            text-align: center;
            line-height: 40px;
            border-radius: 40%;
            color: white;
            text-decoration: none;
            gap: 20px;

        }

   
    }





    //home 
    .home {
        .posts {
            margin-top: 50px;
            display: flex;
            justify-content: space-evenly;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 20px; // Zmniejszona przerwa między postami
            width: 100%; // Pełna szerokość kontenera
            padding: 15px;
    
            .post {
                display: flex;
                flex-direction: column; // Zmiana na kolumnę, aby zawartość była ułożona pionowo
                width: calc(33.33% - 40px); // Szerokość posta z uwzględnieniem przerw
                background-color: #101010;
                color: white;
                box-shadow: 10px 5px 10px #101010;
                text-align: center;
                border-radius: 2vw;
                margin-top: 20px;
                box-sizing: border-box; // Uwzględnienie paddingu w szerokości
    
                .img {
                    flex: 2;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    img {
                        width: 70%;
                        padding: 20px;
                        display: flex;
                        box-shadow: 2px 8px 12px #101010;
                        align-self: center;
                        justify-content: center;
                        border-radius: 2vw;
                    }
                }
    
                p {
                    font-size: 1.2em;
                    margin: 10px 0; // Dodatkowy margines dla tekstu
                }

                button {
                    border: 2vw;
                    background-color: #101010;
                }
    
                .content {
                    flex: 3;
                    background-color: #101010;
                    display: flex;
                    justify-content: center; // Wyśrodkowanie przycisku
                    align-items: center;
                    padding: 10px; // Dodatkowy padding
                    width: 10%;
                    gap: 20px;
                    button {
                        width: 100%;
                        background-color: green;
                        border-radius: 2vw;
                        border: none;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 10px;
                        font-size: 2em;
                    }   
                }
            }
    }
    




}


    // single 

    .single {
        display: flex;
        justify-content: space-between; /* Rozdziela content i menu na przeciwległych końcach */
        align-items: flex-start; /* Ustawia elementy na górze */
        width: 100%;
        margin: auto;
    
        .content {
            flex: 3; /* Zajmuje większą część strony (3/4) */
            display: flex;
            flex-direction: column;
            gap: 30px;
            padding: 100px;
    
            img {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 10px;
                height: 300px;
                object-fit: cover;
                box-shadow: 2px 5px 10px black;
            }
    
            .user {
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                align-items: center;
                gap: 30px;
                font-size: 1.6em;
                color: white;
    
                img {
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    object-fit: cover;
                }
    
                span {
                    font-weight: bold;
                }
            }
    
            .edit {
                display: flex;
                gap: 50px;
                justify-content: space-evenly;
                width: 20%;
                border-radius: 2vw;
                font-size: 1.5em;
                background-color: red;
                box-shadow: 2px 5px 10px black;
                text-decoration: none;
                color: white;
            }
    
            .description {
                color: white;
                font-size: 1.2em;
                padding: 20px;
    
                h1 {
                    color: white;
                }
    
                p {
                    color: white;
                    text-align: justify;
                    font-size: 1.1em;
                }
            }
        }
    
        .menu {
            flex: 1; /* Zajmuje mniejszą część strony (1/4) */
            display: flex;
            flex-direction: column;
            gap: 25px;
            justify-content: center; /* Ustawia menu na górze */
            align-items: center;
            padding: 50px;
            box-shadow: black 5px 5px 5px;
            background-color: #101010;
            color: white;
    
            h1 {
                font-size: 1.5em;
            }
    
            .post {
                display: flex;
                justify-content: center;
                flex-direction: column;
                gap: 10px;

                h2 {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
    
                img {
                    width: 100%;
                    height: 300px;
                    object-fit: cover;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                button {
                    width: 100%;
                }
            }
        }
    }

    //write 

    .add {
        margin-top: 20px;
        display: flex;

        .content {
            flex: 5;

            input {
                font-size: 2em;
                margin: 50px;
                width: 75%;
            }
        }

        .menu {
            flex: 3;

            gap: 20px;

            h1 {
                text-align: center;
                margin: 50px;
                color: white;
                font-size: 3em;
            }

            span {
                color: white;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 2em;
            }

            label:hover {
                transform: scale(120%);
            }

            label {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 20px;
                background-color: green;
                width: 30%;
                margin: auto;
                margin-top: 20px;
                border-radius: 2vw;
                color: white;
                cursor: pointer;
                transition: 1s;
            }

            .item {
                button {
                    margin: auto;
                    margin-top: 20px;
                }

                .file {
                    text-decoration: underline;
                }
            }

        }

        .editorContainer {
            background-color: white;
            width: 75%;
            border-radius: 2vw;
            margin-left: 10px;
            font-size: 2em;
            min-height: 80vh;
            padding: 50px;
            margin: 20px;
        }
    }

    // slider 

    .slide-container {
        .slider-content {
            .card-wrapper {
                .card {
                    .image-content {
                        .card-image {
                            position: relative;
                            height: 150px;
                            width: 150px;
                            border-radius: 50%;
                        }

                        .card-image .card-img {
                            height: 100%;
                            width: 100%;
                            object-fit: cover;
                        }
                    }
                }
            }
        }
    }

}